body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.github-corner .svg {
  color: #fff;
  position: absolute;
  top: 0;
  border: 0;
  right: 0;
}
.github-corner .octo-arm {
  -webkit-transform-origin: 130px 106px;
          transform-origin: 130px 106px;
}
.github-corner:hover .octo-arm {
  -webkit-animation: octocat-wave 560ms ease-in-out;
          animation: octocat-wave 560ms ease-in-out;
}
@media (max-width: 500px) {
  .github-corner:hover .octo-arm {
    -webkit-animation: none;
            animation: none;
  }
  .github-corner .octo-arm {
    -webkit-animation: octocat-wave 560ms ease-in-out;
            animation: octocat-wave 560ms ease-in-out;
  }
}
@-webkit-keyframes octocat-wave {
  0%, 100% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  20%, 60% {
    -webkit-transform: rotate(-25deg);
            transform: rotate(-25deg);
  }
  40%, 80% {
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
  }
}
@keyframes octocat-wave {
  0%, 100% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  20%, 60% {
    -webkit-transform: rotate(-25deg);
            transform: rotate(-25deg);
  }
  40%, 80% {
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
  }
}

.header {
  text-align: center;
}
.header-logo {
  margin: auto;
  display: block;
  -webkit-animation: header-logo-spin infinite 20s linear;
          animation: header-logo-spin infinite 20s linear;
  height: 300px;
  max-height: 40vmin;
  pointer-events: none;
}
.header-title {
  display: inline-block;
  margin: 20px;
  position: relative;
  background: #ce4040;
  padding: 6px 24px;
  font-weight: 100;
  font-size: 16px;
  color: #fff;
  border-radius: 6px;
}
.header-title:before {
  content: '';
  width: 0;
  height: 0;
  border: 15px solid #ce4040;
  border-left-color: transparent;
  border-right-color: transparent;
  border-top-color: transparent;
  position: absolute;
  bottom: 100%;
  left: 50%;
  margin-left: -15px;
}
@-webkit-keyframes header-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes header-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.form {
  width: 500px;
  line-height: 3;
}
.form-input {
  font-size: 14px;
  color: #fff;
  background: transparent;
  box-sizing: border-box;
  display: block;
  width: 100%;
  border: 1px solid #ce4040;
  padding: 5px 10px;
  width: 100%;
  display: block;
}
.form-input:-webkit-input-placeholder {
  color: #eee;
}
.form-link,
.form-link:link,
.form-link:visited,
.form-link:hover,
.form-link:active {
  color: #61dafb;
}

.App {
  color: #fff;
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 14px;
}
.App-link {
  color: #61dafb;
}

